import './App.css';
import Intro from './components/Intro';
import { useState } from 'react';
import Testemonial from './Testemonial';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

function App() {

  const [appName, setAppName] = useState('')
  const [appTestemonial, setAppTestemonial] = useState('')

  const getData = (data) => {
    setAppName(data.name)
    setAppTestemonial(data.testemonial)
    console.log(appTestemonial)
  }
  return (
    <>
    <Router>
      <Switch>
    <Route path="/" exact>
      <Intro getData={getData}/>
      </Route>
    <Route path="/testemonial">
      <Testemonial testemonial={appTestemonial} name={appName}/>
    </Route>
    </Switch>
    </Router>
    </>
  );
}

export default App;
