import React from 'react'
import { useState } from 'react'
import { Link } from 'react-router-dom'

const EnterForm = ({getData}) => {

    const [name, setName] = useState('')
    const [testemonial, setTestemonial] = useState('')

    const onSubmit = () => {

        getData({name, testemonial})
    }

    return (
        <form className="main-form">
            <div>
            <label className="name-label" htmlFor="name">Name</label>
            <input id="name" value={name} onChange={(e) => setName(e.target.value)} type="text" />
            </div>
            <textarea name="" value={testemonial} onChange={(e) => setTestemonial(e.target.value)} id="" cols="30" rows="10"></textarea>
            <Link to='/testemonial'>
            <button onClick={onSubmit}>Submit</button>
            </Link>
        </form>
    )
}

export default EnterForm
