import React from 'react'
import EnterForm from './EnterForm'

const Intro = ({getData}) => {

    return (
        <div className="intro">
            <div className="welcome-area">
                <i className="fas fa-book-reader"></i>
                <h1 className="intro-heading">Thank you for considering to write a testemonial!</h1>
                <p>Please enter your name and continue</p>
                <img src='' alt="" />
            </div>
            <EnterForm getData={getData}/>
        </div>
    )
}

export default Intro
